.App {
  text-align: center;
  justify-content: center;
  margin-right: 50px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: green;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

.App-link {
  color: green;
}

.chatNameBlock {
  margin: auto;
  padding-left: 10%;
}

p {
  color: gold;
  font-size: 130%;
}

.onlineperson {
  width: 100%;
  height: 20px;
  color: gold;
}

#secBlock {
  display: flex;
  flex-direction: column;
}

.me {
  right: 5%;
  margin-left: 60%;
}

.others {
  left: 5%;
  margin-right: 60%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
